.gallery {
  padding: 130px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  .gallery-content {
    &__items {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      margin-top: 60px;

      @media screen and (max-width: $media-tablet) {
        flex-direction: column;
        align-items: center;
      }

      .item {
        margin-bottom: 30px;

        img {
          width: 100%;
        }
      }

      .item-last {
        margin: 0;

        @media screen and (max-width: $media-tablet) {
          margin-bottom: 30px;
        }
      }
    }

    &__items_hidden {
      display: none;
    }

    .more {
      margin: 75px 0 0;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      font-size: 20px;
      line-height: 20px;
      color: #52503b;
      font-weight: 600;
      cursor: pointer;
      padding: 15px 20px;
      border: 1px solid #52503b;
      border-radius: 60px;
      text-transform: uppercase;
      font-family: "Montserrat", sans-serif;
      background: transparent;

      @media screen and (max-width: $media-tablet) {
        margin: 0;
      }
    }
  }
}
