.materials {
  background-image: url("./assets/images/materials/back.webp");
  background-size: cover;
  object-fit: cover;
  margin-top: 100px;
  padding: 150px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
    margin: 0;
  }

  .materials-content {
    text-align: center;
    position: relative;

    .list-materials {
      background: #f1f1f1;
      border-radius: 44px;
      padding: 6px;
      margin: 40px auto 60px;
      display: inline-block;

      @media screen and (max-width: $media-mobile) {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        padding: 12px;
        margin: 20px 0 40px;
      }

      .item {
        font-size: 18px;
        line-height: 33px;
        font-weight: 400;
        color: #1e1e1e;
        padding: 6px 24px;
        transition: 0.3s;
        border-radius: 32px;

        &:hover {
          background: #fff;
        }
      }

      .item.active {
        background: #ffffff;
        border-radius: 32px;
      }

      .item.disabled {
        opacity: 0.8;
        cursor: default;
        pointer-events: none;
      }
    }

    .materials-swiper {
      max-height: 422px;
      position: static;

      .swiper-button-next,
      .swiper-button-prev {
        position: absolute;
        top: 67%;
        width: auto;
        height: auto;

        &::after {
          display: none;
        }

        @media screen and (max-width: 1480px) {
          display: none;
        }
      }

      .swiper-button-next {
        right: -90px;
      }

      .swiper-button-prev {
        left: -90px;
        transform: rotate(180deg);
      }
    }
  }
}
