.header {
  background-image: url("./assets/images/header/back.webp");
  background-size: cover;
  object-fit: cover;
  padding: 0 0 65px;

  @media screen and (max-width: $media-tablet) {
    padding: 0 0 35px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 45px 0;

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
    }

    @media screen and (max-width: $media-mobile) {
      flex-direction: row;
      padding: 15px 20px 0;
    }

    &_nav {
      margin-left: 220px;

      @media screen and (max-width: $media-tablet) {
        margin: 30px 0 0;
      }

      @media screen and (max-width: $media-mobile) {
        display: none;
      }

      .item {
        font-size: 14px;
        line-height: 21px;
        font-weight: 400;
        text-transform: uppercase;
        margin-right: 27px;
        color: #fff;
      }

      .item:last-of-type {
        margin: 0;
      }
    }

    &_communication {
      display: flex;
      align-items: center;

      @media screen and (max-width: $media-tablet) {
        margin: 30px 0 0;
      }

      @media screen and (max-width: $media-mobile) {
        display: none;
      }

      .left-part {
        display: flex;
        align-items: center;
        margin-right: 32px;
        position: relative;

        .icon {
          margin-right: 15px;
        }

        .icon-down {
          margin-left: 7px;
          transition: 0.3s;
        }

        .icon-down.open {
          transform: scale(1, -1);
        }

        .tel {
          font-size: 16px;
          line-height: 24px;
          font-weight: 700;
          color: #fff;
          display: flex;
          align-items: center;

          svg {
            margin-right: 15px;
          }
        }
      }

      .list {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 40px;
        transition: 0.3s;
      }

      .list.visible {
        opacity: 1;
        visibility: visible;
      }
    }

    .burger {
      width: 25px;
      height: 20px;
      position: absolute;
      background: transparent;
      border: none;
      display: none;
      transition: all 0.3s;
      z-index: 11;
      right: 20px;
      top: 50px;

      @media screen and (max-width: $media-mobile) {
        display: block;
      }

      &__line {
        width: 100%;
        height: 2px;
        background: #f19817;
        position: absolute;
        left: 0;
        transition: all 0.3s;
        border-radius: 2px;
      }

      &__line_top {
        top: 0;
      }

      &__line_middle {
        top: 50%;
        transform: translateY(-50%);
      }

      &__line_bottom {
        bottom: 0;
      }
    }

    .burger.active {
      position: relative;
      top: 0px;
      right: 0;

      .burger__line_top {
        transform: rotate(-45deg);
        top: 0;
        left: 0;
        background: #f19817;
        z-index: 11;
        position: absolute;
      }

      .burger__line_middle {
        display: none;
      }

      .burger__line_bottom {
        transform: rotate(45deg);
        top: 0;
        left: 0;
        background: #f19817;
        z-index: 11;
        position: absolute;
      }
    }
  }

  &__main {
    text-align: center;
    margin-top: 150px;

    @media screen and (max-width: $media-tablet) {
      margin-top: 50px;
    }

    &_title {
      font-size: 60px;
      line-height: 73px;
      font-weight: 700;
      color: #fff;
      max-width: 996px;
      text-transform: uppercase;
      margin: 0 auto;

      @media screen and (max-width: $media-tablet) {
        font-size: 32px;
        line-height: 42px;
        max-width: 500px;
      }
    }

    &_paragraph {
      font-size: 18px;
      line-height: 31px;
      font-weight: 400;
      color: #fff;
      max-width: 571px;
      margin: 45px auto 0;

      @media screen and (max-width: $media-tablet) {
        font-size: 16px;
        line-height: 26px;
        margin: 20px auto 0;
      }
    }

    &_form {
      margin-top: 80px;
      display: flex;
      align-items: center;
      justify-content: center;

      @media screen and (max-width: $media-tablet) {
        flex-direction: column;
        margin-top: 40px;
      }

      .input {
        background: rgba(255, 255, 255, 0.28);
        border: 0.861538px solid rgba(255, 255, 255, 0.6);
        backdrop-filter: blur(4px);
        border-radius: 42px;
        outline: none;
        font-size: 16px;
        color: #fff;
        line-height: 24px;
        padding: 15px 20px;
        font-weight: 400;
        width: 345px;

        &::placeholder {
          font-size: 16px;
          color: #fff;
          line-height: 24px;
          font-weight: 400;
        }
      }

      .input:first-of-type {
        margin-right: 10px;

        @media screen and (max-width: $media-tablet) {
          margin: 0 0 10px;
        }
      }

      .btn-back-call {
        margin-left: 35px;

        @media screen and (max-width: $media-tablet) {
          margin: 10px 0 0;
        }
      }
    }

    .button-to-bottom {
      margin: 180px auto 0;
      display: block;

      @media screen and (max-width: $media-tablet) {
        margin: 90px auto 0;
      }

      @media screen and (max-width: $media-mobile) {
        display: none;
      }
    }
  }
}
