.our-services {
  padding: 130px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  .our-services-content {
    position: relative;

    .our-services-swiper {
      margin-top: 60px;
      max-height: 422px;
      position: static;

      @media screen and (max-width: $media-tablet) {
        margin-top: 30px;
      }

      .swiper-button-next,
      .swiper-button-prev {
        position: absolute;
        top: 50px;
        width: auto;
        height: auto;

        &::after {
          display: none;
        }

        @media screen and (max-width: $media-tablet) {
          display: none;
        }
      }

      .swiper-button-next {
        right: 0;
      }

      .swiper-button-prev {
        right: 70px;
        left: auto;
        transform: rotate(180deg);
      }
    }
  }
}
