.about-us {
  padding-top: 100px;

  @media screen and (max-width: $media-tablet) {
    padding-top: 50px;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
      align-items: center;
    }

    &_left-part {
      width: 491px;

      @media screen and (max-width: $media-tablet) {
        width: 100%;
      }
    }

    &_right-part {
      width: 561px;

      @media screen and (max-width: $media-tablet) {
        width: 100%;
        margin-top: 20px;
      }

      .paragraph {
        color: #333333;
        font-size: 16px;
        line-height: 22px;
        font-weight: 400;
        margin-bottom: 25px;

        @media screen and (max-width: $media-tablet) {
          margin-bottom: 15px;
        }
      }

      .badge-profile {
        margin-top: 45px;
        display: flex;
        align-items: center;

        @media screen and (max-width: $media-tablet) {
          margin-top: 25px;
        }

        .info {
          margin-left: 15px;

          .name {
            color: #45525b;
            font-size: 16px;
            line-height: 22px;
            font-weight: 400;
          }

          .function {
            color: #6f7278;
            font-size: 12px;
            line-height: 17px;
            font-weight: 400;
          }
        }
      }
    }
  }
}
