.footer {
  background: #292c32;
  padding: 110px 0 60px;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
    }

    .item {
      color: #ffffff;
      font-size: 15px;
      line-height: 24px;
      font-weight: 400;
      margin-top: 16px;

      @media screen and (max-width: $media-tablet) {
        margin-top: 10px;
      }
    }

    .heading {
      color: #f19817;
      font-size: 14px;
      line-height: 20px;
      font-weight: 600;
      margin-bottom: 6px;
      text-transform: uppercase;
    }

    &_left-part {
      width: 330px;
      margin-right: 170px;

      @media screen and (max-width: $media-tablet) {
        margin: 0;
      }

      .item {
        margin-top: 30px;
      }
    }

    &_services,
    &_page,
    &_follow-us {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      @media screen and (max-width: $media-tablet) {
        margin: 30px 0 0;
      }
    }

    &_follow-us {
      .item {
        display: flex;
        align-items: center;

        svg {
          margin-right: 8px;
        }
      }
    }
  }

  &__copyright {
    margin-top: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: $media-tablet) {
      margin: 30px 0 0;
      flex-direction: column-reverse;
      align-items: flex-start;
    }

    .item {
      color: #ffffff;
      font-size: 15px;
      line-height: 24px;
      font-weight: 400;
    }

    &_left {
      @media screen and (max-width: $media-tablet) {
        margin-top: 10px;
      }

      .item {
        line-height: 19px;
      }
    }

    &_right {
      display: flex;
      align-items: center;

      @media screen and (max-width: $media-tablet) {
        flex-direction: column;
        align-items: flex-start;
      }

      .item:last-of-type {
        margin-left: 30px;

        @media screen and (max-width: $media-tablet) {
          margin: 10px 0 0;
        }
      }
    }
  }
}
