* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

html,
body {
  margin: 0;
  background: #fafafc;
}

body.active {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.container {
  width: 1290px;
  margin: 0 auto;

  @media screen and (max-width: $media-tablet) {
    width: 100%;
    padding: 0 20px;
  }
}

.btn-back-call {
  background: #f19817;
  border-radius: 55px;
  padding: 15px 25px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #fff;
  outline: none;
  cursor: pointer;
  border: none;
}

.btn-back-call:disabled {
  opacity: 0.7;
}

.title-blue {
  color: #2877ba;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  font-weight: 600;
}

.title-gray {
  color: #45525b;
  font-size: 48px;
  line-height: 53px;
  font-weight: 500;
  margin-top: 15px;

  @media screen and (max-width: $media-tablet) {
    font-size: 32px;
    line-height: 42px;
  }
}

.slide-block {
  background: #fff;
  border-radius: 20px;
  width: 300px;
  height: 422px;
  text-align: start;

  @media screen and (max-width: $media-tablet) {
    text-align: center;
  }

  .block-wrapper {
    padding: 20px 20px 25px;

    .name {
      color: #6f7278;
      font-size: 12px;
      line-height: 16px;
      font-weight: 400;
    }

    .description {
      color: #45525b;
      font-size: 20px;
      line-height: 24px;
      font-weight: 500;
      margin-top: 5px;
    }

    .bottom-part {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 40px;

      .price {
        display: flex;
        align-items: center;

        .item:first-of-type {
          color: #0d1b3970;
          font-size: 13px;
          line-height: 16px;
          font-weight: 400;
          margin: 0 5px 10px 0;
        }

        .item {
          color: #45525b;
          font-size: 21px;
          line-height: 26px;
          font-weight: 600;
          display: block;
        }

        .item:last-of-type {
          font-size: 15px;
          line-height: 18px;
          font-weight: 600;
          margin: 10px 0 0 5px;
        }
      }

      .plus {
        background: #f19817;
        border-radius: 31px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 15px;
        cursor: pointer;
      }
    }
  }
}

.block-img {
  img {
    width: 100%;
  }
}

.mobile-menu {
  background: #292c32;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  transition: 0.3s;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10;
  padding: 20px;
  overflow-y: scroll;

  .nav {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .item {
      font-size: 14px;
      line-height: 21px;
      font-weight: 400;
      text-transform: uppercase;
      margin-top: 20px;
      color: #fff;
    }
  }

  .communication {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 30px;

    .left-part {
      display: flex;
      align-items: center;
      margin-bottom: 20px;

      .icon {
        margin-right: 15px;
      }

      .tel {
        font-size: 16px;
        line-height: 24px;
        font-weight: 700;
        color: #fff;
      }
    }
  }
}

.mobile-menu.open {
  transform: translateX(0);
}

.button-to-top {
  display: none;
  position: fixed;
  bottom: 25px;
  right: 10px;
  z-index: 1;
  border: none;
  outline: none;
  background: #777;
  cursor: pointer;
  padding: 8px;
  border-radius: 50%;
  transition: 0.3s;
  text-align: center;
  font-size: 20px;
  color: #fff;
  line-height: 1em;
  background: transparent;

  svg {
    transform: rotate(180deg);
  }
}

.swal2-confirm.swal2-styled {
  background-color: #f19817;
}
