.testimonials {
  background: #fff;
  padding: 150px 0;
  position: relative;
  overflow-y: hidden;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  .banner-testimonials {
    position: absolute;
    left: 0;
    top: -4px;
    display: block;
    height: 100%;

    @media screen and (max-width: $media-tablet) {
      display: none;
    }

    img {
      display: block;
      height: 100%;
    }
  }

  .testimonials-content {
    position: relative;

    .testimonials-swiper {
      margin-top: 60px;
      position: static;

      @media screen and (max-width: $media-tablet) {
        margin-top: 30px;
      }

      .testimonials-swiper-wrapper {
        height: auto;

        .testimonials-slide {
          background: #ffffff;
          border: 1px solid rgba(82, 80, 59, 0.1);
          border-radius: 4px;
          padding: 30px;

          .title {
            margin-top: 10px;
            font-weight: 400;
            color: #333333;
            font-size: 24px;
            font-weight: 28px;
          }

          .paragraph {
            margin: 20px 0;
            font-weight: 400;
            color: #333333;
            font-size: 15px;
            font-weight: 24px;
          }

          .name {
            font-weight: 400;
            color: #45525b;
            font-size: 16px;
            font-weight: 16px;
            display: block;
            border-left: 2px solid #f19817;
            padding-left: 10px;
          }
        }
      }

      .swiper-button-next,
      .swiper-button-prev {
        position: absolute;
        top: 50px;
        width: auto;
        height: auto;

        &::after {
          display: none;
        }

        @media screen and (max-width: $media-tablet) {
          display: none;
        }
      }

      .swiper-button-next {
        right: 0;
      }

      .swiper-button-prev {
        right: 70px;
        left: auto;
        transform: rotate(180deg);
      }
    }
  }
}
