.contacts {
  background: #45525b;
  padding: 110px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  .contacts-content {
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
    }

    .info {
      .item {
        .name {
          color: #ffffff4d;
          font-size: 14px;
          line-height: 20px;
          font-weight: 600;
          text-transform: uppercase;
          margin-bottom: 10px;
        }

        .contact {
          color: #ffffff;
          font-size: 20px;
          line-height: 33px;
          font-weight: 400;

          @media screen and (max-width: $media-tablet) {
            font-size: 18px;
            line-height: 30px;
          }
        }
      }

      .item:last-of-type {
        margin-top: 45px;

        @media screen and (max-width: $media-tablet) {
          margin-top: 20px;
        }
      }
    }

    .form {
      .inputs {
        display: flex;
        align-items: center;

        @media screen and (max-width: $media-tablet) {
          flex-direction: column;
          align-items: flex-start;
        }

        .input {
          background: #ffffff;
          border: 1px solid #eeeeee;
          border-radius: 40px;
          font-size: 14px;
          line-height: 21px;
          font-weight: 400;
          color: #999999;
          padding: 18px 30px;
          border: none;
          outline: none;
          width: 305px;

          @media screen and (max-width: $media-tablet) {
            margin: 20px 0 0;
          }

          @media screen and (max-width: $media-mobile) {
            width: 100%;
          }

          &::placeholder {
            font-size: 14px;
            line-height: 21px;
            font-weight: 400;
            color: #999999;
          }
        }

        .input:last-of-type {
          margin-left: 20px;

          @media screen and (max-width: $media-tablet) {
            margin: 20px 0 0;
          }
        }
      }

      .textarea {
        background: #ffffff;
        border: 1px solid #eeeeee;
        border-radius: 40px;
        font-size: 14px;
        line-height: 21px;
        font-weight: 400;
        color: #999999;
        padding: 18px 30px;
        border: none;
        outline: none;
        resize: none;
        width: 100%;
        height: 128px;
        margin: 20px 0 25px;

        &::placeholder {
          font-size: 14px;
          line-height: 21px;
          font-weight: 400;
          color: #999999;
        }
      }
    }
  }
}
