.statistics {
  background: #fff;
  padding: 150px 0;
  position: relative;
  overflow: hidden;

  @media screen and (max-width: $media-tablet) {
    padding: 50px 0;
  }

  .banner {
    text-transform: uppercase;
    color: rgba(69, 82, 91, 0.05);
    transform: rotate(-21deg);
    display: block;
    position: absolute;
    font-size: 131px;
    line-height: 145px;
    font-weight: 700;
  }

  .banner-top {
    top: 0;
    left: -60px;
  }

  .banner-bottom {
    bottom: 0px;
    right: -50px;
  }

  .statistics-content {
    text-align: center;

    .paragraph {
      color: #6f7278;
      font-size: 18px;
      line-height: 22px;
      font-weight: 400;
      max-width: 561px;
      margin: 30px auto 0;

      @media screen and (max-width: $media-tablet) {
        font-size: 16px;
        line-height: 20px;
        margin: 20px auto 0;
      }
    }

    &__main {
      margin: 60px 0 0 100px;
      display: flex;
      align-items: center;

      @media screen and (max-width: $media-tablet) {
        margin: 30px 0 0;
        flex-direction: column;
      }

      &_img {
        img {
          width: 100%;
        }
      }

      &_list {
        margin-left: 90px;

        @media screen and (max-width: $media-tablet) {
          margin: 0;
        }

        .item {
          padding: 23px;
          border-radius: 10px;
          width: 300px;
          text-align: start;
          transition: 0.3s;
          border: 1px solid transparent;
          margin-top: 20px;

          @media screen and (max-width: $media-tablet) {
            width: 100%;
          }

          &:hover {
            background: #fafafc;
            border: 1px solid rgba(202, 204, 209, 0.2);
          }

          .item-number {
            color: #2877ba;
            font-size: 30px;
            line-height: 37px;
            font-weight: 700;
          }

          .item-title {
            color: #45525b;
            font-size: 14px;
            line-height: 20px;
            font-weight: 400;
            margin-top: 20px;
          }

          .item-subtitle {
            color: #6f7278;
            font-size: 14px;
            line-height: 20px;
            font-weight: 400;
            margin-top: 5px;
          }
        }

        .item:first-of-type {
          margin: 0;

          @media screen and (max-width: $media-tablet) {
            margin: 20px 0 0;
          }
        }
      }
    }
  }
}
